import React from "react";
import { useLanguage } from "../../contexts/LanguageContext";
import { contactInfo, productGroups } from "../../mock";
import { Mail, Phone, MapPin, Facebook, Instagram, Linkedin, Youtube, MessageCircle } from "lucide-react";
import { Link, NavLink } from "react-router-dom";

const Footer = () => {
    const { lang } = useLanguage();

    return (
        <footer className="bg-[#323232] pt-16 pb-8 text-white border-t border-white/5">
            <div className="mx-auto max-w-6xl px-4">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 mb-12">
                    {/* About Section */}
                    <div className="space-y-6">
                        <h3 className="text-sm font-bold tracking-[0.2em] uppercase text-white">
                            {lang === "tr" ? "PROFNOKS HAKKINDA" : "ABOUT PROFINOKS"}
                        </h3>
                        <p className="text-sm text-white leading-relaxed">
                            {lang === "tr"
                                ? "2010 ylndan beri endstriyel mutfak ekipmanlar retimi ve montaj konusunda hizmet vermekteyiz. Kaliteli retim anlaymz ve mteri memnuniyeti odakl alma prensibimizle sektrde nc firmalardan biriyiz."
                                : "Since 2010, we have been providing services in the production and installation of industrial kitchen equipment. We are one of the leading companies in the sector with our quality production approach and customer satisfaction-oriented working principle."}
                        </p>
                        <div className="flex space-x-4 pt-2">
                            <a href="https://www.facebook.com/profinoks.endustriyelmutfakekipmanlari" target="_blank" rel="noreferrer" className="text-white/40 hover:text-white transition-colors" title="Facebook"><Facebook size={18} /></a>
                            <a href="https://www.instagram.com/profinoks/?hl=tr" target="_blank" rel="noreferrer" className="text-white/40 hover:text-white transition-colors" title="Instagram"><Instagram size={18} /></a>
                            <a href="https://www.linkedin.com/in/profinoks-end%C3%BCstriyel-mutfak-ekipmanlari-4471921b2/" target="_blank" rel="noreferrer" className="text-white/40 hover:text-white transition-colors" title="LinkedIn"><Linkedin size={18} /></a>
                            <a href="https://www.youtube.com/channel/UCTsoSgntPEXAshH80VtSa-g/featured" target="_blank" rel="noreferrer" className="text-white/40 hover:text-white transition-colors" title="YouTube"><Youtube size={18} /></a>
                            <a href="https://api.whatsapp.com/send?phone=905306140705" target="_blank" rel="noreferrer" className="text-white/40 hover:text-white transition-colors" title="WhatsApp"><MessageCircle size={18} /></a>
                        </div>
                    </div>

                    {/* Quick Links */}
                    <div className="space-y-6">
                        <h3 className="text-sm font-bold tracking-[0.2em] uppercase text-white">
                            {lang === "tr" ? "HIZLI BALANTILAR" : "QUICK LINKS"}
                        </h3>
                        <ul className="space-y-4 text-sm text-white">
                            <li><Link to="/corporate" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "Kurumsal" : "Corporate"}</span></Link></li>
                            <li><Link to="/products" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "rnler" : "Products"}</span></Link></li>
                            <li><Link to="/references" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "Referanslar" : "References"}</span></Link></li>
                            <li><Link to="/projects" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "Projeler" : "Projects"}</span></Link></li>
                            <li><Link to="/news" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "Haberler" : "News"}</span></Link></li>
                            <li><Link to="/contact" className="hover:text-white transition-colors flex items-center"> <span className="ml-2">{lang === "tr" ? "letiim" : "Contact"}</span></Link></li>
                        </ul>
                    </div>

                    {/* Contact Info */}
                    <div className="space-y-6">
                        <h3 className="text-sm font-bold tracking-[0.2em] uppercase text-white">
                            {lang === "tr" ? "LETM BLGLER" : "CONTACT INFO"}
                        </h3>
                        <ul className="space-y-4 text-sm text-white">
                            <li className="flex items-start">
                                <MapPin size={18} className="mr-3 text-white shrink-0 mt-0.5" />
                                <span>{contactInfo.address}</span>
                            </li>
                            {contactInfo.phone.map((p, idx) => (
                                <li key={idx} className="flex items-center">
                                    <Phone size={18} className="mr-3 text-white shrink-0" />
                                    <a href={`tel:${p.replace(/\s/g, "")}`} className="hover:text-white transition-colors">{p}</a>
                                </li>
                            ))}
                            {contactInfo.emails.map((e, idx) => (
                                <li key={idx} className="flex items-center">
                                    <Mail size={18} className="mr-3 text-white shrink-0" />
                                    <a href={`mailto:${e}`} className="hover:text-white transition-colors">{e}</a>
                                </li>
                            ))}
                        </ul>
                    </div>

                    {/* Working Hours */}
                    <div className="space-y-6">
                        <h3 className="text-sm font-bold tracking-[0.2em] uppercase text-white">
                            {lang === "tr" ? "ALIMA SAATLER" : "WORKING HOURS"}
                        </h3>
                        <div className="bg-white/5 p-4 rounded-sm border border-white/10">
                            <p className="text-sm text-white/80 leading-relaxed font-medium">
                                {contactInfo.workingHours}
                            </p>
                        </div>
                    </div>
                </div>

            </div>

            <div className="bg-black py-4 mt-8">
                <div className="mx-auto max-w-6xl px-4 flex flex-col md:flex-row justify-between items-center text-xs text-white/80 gap-4">
                    <p> {new Date().getFullYear()} Profinoks Endstriyel Mutfak. Tm haklar sakldr.</p>
                    <div className="flex space-x-6">
                        <Link to="/privacy" className="hover:text-white transition-colors">Privacy Policy</Link>
                        <Link to="/terms" className="hover:text-white transition-colors">Terms of Service</Link>
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
